body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    font-display: fallback;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer */
    -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
    -webkit-user-select: none; /* Chrome, Safari, and Opera */
    -webkit-touch-callout: none; /* Disable Android and iOS callouts*/
}
.dashboard-page{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 50px calc(100vw - 84px) 150px 120px;
    grid-row-gap: 15px;
    row-gap: 15px;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
            column-gap: 15px;
    padding: 40px 40px 40px 40px;
    background: linear-gradient(180deg, #FC8A8A 0%, #FFD3B3 50%);
    background-size: 100vw 190px;
    background-repeat: no-repeat;
}
.chart-holder{
    z-index: 0;
    border: 2px solid #FFDCDC;
    grid-column: 1/4;
    grid-row: 2/3;
}
.dashboard-page--title{
    grid-column: 1/4;
    grid-row: 1/2;
}
.month-total-money{
    text-align: center;
    background-color: #FFAFA4 !important;
    grid-column: 1/3;
    grid-row: 3/4;
}
.month-total-money > p {
    color: white;
    font-size: 12px;
    padding: 15px 20px 10px 20px;
}
.month-drink-limit{
    background: linear-gradient(180deg, #FFDCDC 0%, #FFDCDC 50%) no-repeat;
    background-position-y: 300px;
    background-size: 100% 100%;
    text-align: center;
    padding: 80px 3px 0 3px;
    border: 2px solid #FFDCDC;
    grid-column: 3/4;
    grid-row: 3/5;
    transition: all 1s;
}
.month-drink-limit > p{
    color: #FC8A8A;
    font-size: 12px;
    mix-blend-mode: darken;
}
.month-total-drinks{
    padding: 20px;
    text-align: center;
    grid-column: 1/3;
    grid-row: 4/5;
}
.daily-chart{
    touch-action: none;
    grid-column: 1/4;
}
.month-total-drinks > p {
    color: #FC8A8A;
}
.month-total-drinks > h2 {
    color: #FC8A8A;
}
.chard-holder-description{
    font-family: 'Poppins';
    position: absolute;
    text-align: center;
    margin-top: calc(50vw - 80px);
    z-index: 1;
    width: calc(100vw - 84px);
    font-size: 11px;
}
.chard-holder-description span{
    font-weight: bold;
    font-size: 40px;
    line-height: 40px;
}
