.add-modal{
    position: fixed;
    top: calc( 50vh - 230px);
    height: 390px;
    width: calc( 100vw - 84px);
    max-width: 420px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: white;
    border: 2px solid #FFDCDC;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 27px;
}
.add-input{
    width: 100%;
}
.add-button-holder{
    text-align: center;
    width: 100%;
}
.close-button{
    position: absolute !important;
    top: 15px;
    right: 15px;
    padding: 3px !important;
    background-color: #FFAFA4 !important;
}
.clipboard{
    border: 1px solid #FFDCDC;
    border-radius: 2px;
    padding: 3px 0 3px 10px;
    font-size: 14px;
    display: grid;
    grid-template-columns: 1fr 25px;
}
.clipboard-text{
    font-family: 'Poppins';
    -webkit-user-select: all;
       -moz-user-select: all;
        -ms-user-select: all;
            user-select: all;
    overflow-x: scroll;
}
.red{
    color: #FF0000;
}
.user-modal{
    position: fixed;
    top: calc( 50vh - 190px);
    width: calc( 100vw - 50px);
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    max-width: 400px;
    background-color: white;
    border: 3px solid #FFDCDC;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 60px 40px 40px 40px;
    transition: all 320ms;
}
.user-modal:focus{
    outline: none;
}
.user-input{
    width: 100%;
}
.update-button-holder{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
    width: 100%;
}
.user-avatar{
    border-radius: 50%;
    height: 100px;
    width: 100px;
    position: absolute;
    top: -60px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin: 0;
    border: 4px solid #FFDCDC;
}
.close-button{
    position: absolute !important;
    top: 15px;
    right: 15px;
    padding: 3px !important;
    background-color: #FFAFA4 !important;
}
.user-share-profile{
    padding-left: 20px;
    text-align: center;
}
.logout-button{
    transition: all 320ms !important;
}
.logout-button:hover{
    background-color: #FF5050 !important;
    color: white !important;
}
.history-page{
    padding: 40px 40px 40px 40px;
}
.history-drink-label{
    display: grid;
    grid-template-columns: 1fr 1fr 40px;
    grid-template-rows: 25px 35px;
}
p{
    margin: 0 !important;
    font-family: Poppins;
}
.drink-place{
    color: #F68080;
}
.drink-time{
    text-align: right;
    color: grey;
    font-size: 12px;
    padding-top: 8px;
}
.drink-price{
    font-size: 24px;
    text-align: right;
}
.drink-description{
    padding-top: 10px;
    padding-bottom: 10px;
}
img{
    width: 60vw;
    background-color: #FFC9C2;
    margin: 30px calc(50% - 30vw) 0 calc(50% - 30vw);
}
.thaman-color{
    border-bottom: 2px solid #F68080;
    margin-bottom: 10px;
}
h3 span{
    color: black;
}
.history-total{
    margin-top: 25px !important;
    text-align: right;
}
.expand-icon{
    margin-top: 10px;
    color: #F68080;
}
.drink-options{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 60px;
    margin-bottom: 5px;
}
.drink-label{
    font-weight: bold;
    text-align: center;
}
.drink-label span{
    color: #8888FF;
    text-align: center;
}
.drink-date{
    text-align: center;
}
.drink-date span{
    color: #8888FF;
    font-weight: bold;
    font-size: 14px;

}
.drink-collapse{
    text-align: center;
}
.login-page{
    height: 100vh;
    width: 100vw;
    text-align: center;
    background: linear-gradient(180deg, #FC8A8A 0%, #FFD3B3 100%);
}
.login-logo{
    height: 100vw;
    max-height: 50vh;
    width: 100vw;
    max-width: 50vh;
    margin: 0 auto;
    padding-top: 20vh;
    background-image: url(/static/media/logo-shadow.0824830a.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}
.bottom-nav{
    width: 100%;
    position: fixed;
    bottom: 0;
}
.center-fab{
    height: 100px;
    width: 100px;
    box-sizing: border-box;
    padding-top: 17px;
    margin-top: -35px;
    text-align: center;
    border: 5px solid #FFAFA4;
    border-radius: 50%;
    color: #FFAFA4;
    background-color: white;
    z-index: 1000;
}
.page{
    height: calc(100vh - 56px);
    box-sizing: border-box;
    width: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-bottom: 40px;
    position: relative;
}
.avatar-button{
    height: 37px;
    width: 37px;
    border-radius: 50%;
    position: absolute;
    right: -36px;
    top: 5px;
}
@-moz-document url-prefix() {
    .center-fab{
        margin-top: -5px;
    }
}
@media only screen and (max-width: 350px) {
    h2 {
        font-size: 30px !important;
    }
    h3 {
        font-size: 20px !important;
    }
    h4 {
      font-size: 20px !important;
    }
  }

